html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
  font-family: "Source Sans Pro", sans-serif;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #00b9aa;
}

.title-bg {
  background-color: #FFFFFF;
  width: 100%;
  color: #dd5800;
  display: block;
}

.center {
  display: flex;
  margin: 0 auto;
}

.footer {
  background-color: #232F3E;
  margin: 0;
  margin-top: 2rem;
  padding: 2rem 0;
}
.footer .footer-padre ul {
  margin: 0;
}
.footer .footer-padre li {
  list-style: none;
  color: #FFFFFF;
}
.footer .footer-padre p {
  margin: 0;
  color: #FFFFFF;
  font-size: 1.8rem;
}
.footer .footer-padre h4 {
  color: #dd5800;
  font-size: 2.5rem;
  margin: 0;
  font-weight: bold;
  text-shadow: 1px 1px 1px black;
}
@media (min-width: 768px) {
  .footer .footer-padre .footer-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
.footer .footer-padre .footer-grid .caja {
  box-shadow: 2px 2px 2px black;
  text-align: center;
  background-color: #485769;
  border-radius: 2rem;
  padding: 1rem;
  margin: 2rem;
}
.footer .footer-padre .footer-grid .caja h4 {
  margin: 1rem 0;
}
.footer .footer-padre .footer-grid .caja img {
  width: 15rem;
  align-items: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer .footer-padre .presentacion-footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer .footer-padre .presentacion-footer .redes-sociales {
  background-color: #485769;
  border-radius: 2rem;
  padding: 1rem;
  margin: 2rem;
}
.footer .footer-padre .presentacion-footer .nosotros {
  background-color: #485769;
  border-radius: 2rem;
  padding: 1rem;
  margin: 2rem;
}

.menu {
  background-color: #232F3E;
  padding: 0.5rem 0;
  position: fixed;
  z-index: 500;
  width: 100%;
  align-items: center;
  top: 0;
}

.menu .contenedor-botones-menu {
  display: flex;
  display: none;
  justify-content: space-between;
  margin: 0 1rem;
}

.menu .contenedor-botones-menu button {
  font-size: 2rem;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border: 1px solid transparent;
  display: inline-block;
  cursor: pointer;
  background: none;
}
.menu .contenedor-botones-menu button:hover {
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.menu .contenedor-botones-menu .btn-menu-cerrar {
  display: none;
}

.menu .contenedor-botones-menu .btn-menu-cerrar.activo {
  display: inline-block;
}

.menu .contenedor-enlaces-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu .contenedor-enlaces-nav .enlaces a {
  color: #FFFFFF;
  border: 0.1rem solid transparent;
  padding: 0.7rem;
  border-radius: 0.3rem;
  font-size: 1.4rem;
}
.menu .contenedor-enlaces-nav .enlaces a:hover {
  border: 0.1rem solid rgba(255, 255, 255, 0.3);
}

.menu .btn-categorias {
  color: #FFFFFF;
  padding: 0.7rem;
  border-radius: 0.3rem;
  display: flex;
  align-items: flex-end;
  border: 1px solid transparent;
  cursor: default;
}
.menu .btn-categorias p {
  margin: 0;
  color: #FFFFFF;
  font-size: 1.6rem;
}
.menu .btn-categorias i {
  margin-left: 2rem;
  position: relative;
  bottom: 0.3rem;
}
.menu .btn-categorias span {
  display: block;
  font-size: 2.2rem;
  font-weight: bold;
}
.menu .btn-categorias:hover {
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.contenedor-grid {
  position: relative;
}

.grid-cat {
  width: 100%;
  display: none;
  position: absolute;
  top: 7rem;
  z-index: 1000;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-template-areas: "categorias subcategorias subcategorias subcategorias";
}

.grid-cat.activo {
  display: grid;
}

.grid-cat::before {
  content: "";
  display: block;
  background: transparent;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-bottom: 0.5rem solid #FFFFFF;
  position: absolute;
  top: -0.5rem;
  left: 6.5rem;
}

.grid-cat > div {
  background: #FFFFFF;
  box-shadow: 6px 3px 6px rgba(0, 0, 0, 0.1);
}

.grid-cat .btn-regresar {
  background: #232F3E;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 3px;
  margin: 20px;
  font-size: 16px;
  cursor: pointer;
  display: none;
}
.grid-cat .btn-regresar i {
  margin-right: 10px;
}

.grid-cat .categorias {
  grid-area: categorias;
  padding-top: 1rem;
  min-height: 31rem;
  overflow: auto;
}
.grid-cat .categorias .subtitulo {
  display: none;
}
.grid-cat .categorias a {
  color: #000000;
  font-size: 1.4rem;
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
}
.grid-cat .categorias a:hover {
  color: #dd5800;
  font-weight: bold;
}
.grid-cat .categorias a:hover i {
  display: inline-block;
}
.grid-cat .categorias i {
  display: none;
}

.grid-cat .contenedor-subcategorias {
  grid-area: subcategorias;
}

.grid-cat .subcategoria {
  display: none;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  height: 100%;
}
.grid-cat .subcategoria img {
  width: 100%;
  vertical-align: top;
  height: 100%;
  object-fit: cover;
}

.grid-cat .subcategoria.activo {
  display: grid;
}

.grid-cat .enlaces-subcategoria {
  padding-top: 1rem;
  height: 100%;
  max-height: 30rem;
  overflow: auto;
}
.grid-cat .enlaces-subcategoria a {
  color: #000000;
  display: block;
  font-size: 1.4rem;
  padding: 1rem 2rem;
}
.grid-cat .enlaces-subcategoria a:hover {
  color: #dd5800;
}

.grid-cat .subtitulo {
  font-size: 1.8rem;
  font-weight: normal;
  color: #dd5800;
  padding: 1rem 2rem;
  margin: 0;
}

.grid-cat .banner-subcategoria a {
  display: block;
  height: 100%;
}

.grid-cat .galeria-subcategoria {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
.grid-cat .galeria-subcategoria a {
  width: 50%;
  height: 50%;
}

@media screen and (max-width: 1024px) {
  .menu .contenedor-enlaces-nav {
    padding: 0 1rem;
  }

  .menu .contenedor {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .menu .contenedor {
    width: 80%;
  }

  .menu .contenedor-botones-menu {
    display: flex;
  }

  .menu .contenedor-enlaces-nav {
    padding: 2rem;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    left: 0;
    background: #232F3E;
    height: 100vh;
    z-index: 2000;
    width: 80%;
    transition: 0.3s ease all;
    transform: translate(-100%);
  }

  .menu .contenedor-enlaces-nav.activo {
    transform: translate(0%);
  }

  .menu .btn-categorias {
    width: 100%;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  .menu .btn-categorias i {
    position: static;
    margin-left: 2rem;
    transform: rotate(-90deg);
  }

  .menu .enlaces {
    width: 100%;
  }
  .menu .enlaces a {
    display: block;
    margin: 1rem 0;
    padding: 1rem;
  }

  .grid-cat {
    display: grid;
    transition: 0.3s ease all;
    transform: translateX(-100%);
    z-index: 3000;
    width: 80%;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    grid-template-areas: "categorias categorias categorias categorias";
  }
  .grid-cat.activo {
    transform: translateX(0%);
  }
  .grid-cat::before {
    display: none;
  }
  .grid-cat .btn-regresar {
    display: inline-block;
  }
  .grid-cat .categorias .subtitulo {
    display: block;
  }
  .grid-cat .contenedor-subcategorias {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: 0.3s ease all;
    overflow: auto;
  }
  .grid-cat .contenedor-subcategorias.activo {
    transform: translateX(0);
  }
  .grid-cat .contenedor-subcategorias .subcategoria {
    grid-template-columns: 1fr;
  }
  .grid-cat .contenedor-subcategorias .banner-subcategoria {
    width: 100%;
    min-height: 25rem;
    max-height: 35rem;
  }
  .grid-cat .contenedor-subcategorias .enlaces-subcategoria {
    min-height: 50vh;
    overflow: auto;
  }
  .grid-cat .contenedor-subcategorias .galeria-subcategoria a {
    width: 25%;
    height: 100%;
  }
}
